.dashboard {
  width: 100%;
  margin: 0;
  padding: 0;
  display: flex;
}

/* Dashboard Menu */

.dashboard-menu{
    width: 200px;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    border-right: 1px solid black;
    border-bottom: 1px solid black;
    box-sizing: border-box;
    padding-top: 100px;
}

.dashboard-menu .menu-item{
    display: flex;
    align-items: center;
}

.menu-item span{
    font-weight: bold;
    display: flex;
    align-items: center;
    gap: 2px;
    cursor: pointer;
}

.menu-item span:hover{
    color: orange;
}

/* Dashboard Menu */

/*  */

.dashboard-content{
    width: 100%;
    display: flex;
    flex-direction: column;
}

.dashboard-content .header{
    width: 100%;
    height: 100px;
    background-color: aqua;
}
