.uploading-model{
    margin-top: 30px;
    position: relative;
    width: 80%;
    height: 60px;
    display: flex;
}

.progress-bar{
    width: 90%;
    margin: auto;
    align-self: center;
}

.uploading-status{
    position: absolute;
    right: 5%;
    bottom: 0;
}
.uploading-speed{
    position: absolute;
    left: 5%;
    bottom: 0;
}

.cancel-upload{
    position: absolute;
    right: 6%;
    top: 0;
    cursor: pointer;
}

.file-name{
    position: absolute;
    left: 5%;
    top: 0;
    width: 50%;
    white-space: nowrap; /* Prevents the text from wrapping */
    overflow: hidden; /* Hides the overflowing text */
    text-overflow: ellipsis;
}

.tooltip-container {
    position: relative;
    display: inline-block;
    cursor: pointer;
  }
  
  .tooltip-container .tooltip-text {
    visibility: hidden;
    width: 60px;
    background-color: black;
    color: #fff;
    text-align: center;
    border-radius: 4px;
    padding: 5px;
    position: absolute;
    z-index: 1;
    bottom: 125%; /* Position above the icon */
    left: 50%;
    transform: translateX(-50%);
    opacity: 0;
    transition: opacity 0.3s;
  }
  
  .tooltip-container .tooltip-text::after {
    content: "";
    position: absolute;
    top: 100%; /* Bottom of the tooltip */
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: black transparent transparent transparent;
  }
  
  .tooltip-container:hover .tooltip-text {
    visibility: visible;
    opacity: 1;
  }
  