.uploading-form-wrapper {
    display: none;
  position: absolute;
  box-sizing: border-box;
  width: 400px;
  padding: 20px;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.3);
  border-radius: 5px;

  /* Centering */
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.uploading-form-wrapper .cross-close{
    width: 100%;
    display: flex;
    justify-content: flex-end;
    margin-bottom: 10px;
}

.lecture-form {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}

.lecture-form input {
  padding: 10px
}

.lecture-form button{
    padding: 8px;
    border: none;
    border-radius: 2px;
    font-size: 16px;
    font-weight: 700;
    background-color: gold;
    cursor: pointer;
}

.lecture-form button:hover{
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.3);
    background-color: goldenrod;
}

.dialog-open{
    display: block;
}
