.admin-page-wrapper{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.admin-page-wrapper .admin-form{
    width: 300px;
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.admin-form input{
    padding: 10px;
    border: none;
    border: 1px solid black;
    border-radius: 5px;
}

.admin-form button {
    padding: 10px;
    border-radius: 5px;
    background-color: rgb(23, 23, 145);
    color: white;
    cursor: pointer;
    transition: box-shadow 0.3s ease, transform 0.3s ease;
}

.admin-form button:hover {
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.3);
}
